.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 20px;
    z-index: 1;
    transition: all 0.3s ease-in;
  }
  
  
  .nav__content {
    display: flex;
    justify-content: space-between;
  }
  
  .nav_logo {
    position: fixed;
    top: 10px;
    left: 0;
    width: 110px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
   
  }
  
  .nav_avatar {
    cursor: pointer;
    height: 30px;
    width: 30px;
    position: fixed;
    right: 20px;
  }
  