.banner__content {
    margin-left: 50px;
    padding-top: 140px;
    color: aliceblue;
  }
  
  .banner .title {
    font-weight: 800;
    padding-top: 0.3rem;
    font-size: 3rem;
  }
  
  .banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
  }
  
  .banner__fadebottom {
    height: 7.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }
  
  .banner__buttons > button {
    cursor: pointer;
    background-color: rgba(220, 220, 220, 0.384);
    outline: none;
    color: white;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    margin-right: 1rem;
    margin-top: 10px;
    padding: 0 2rem;
    padding: 0.7rem;
  }
  
  .banner__buttons > button:hover {
    color: white;
    background: grey;
    transition: all 0.3s;
  }
  