.row {
    background-color: rgb(0, 0, 0);
    color: whitesmoke;
    font-size: 15px;
    padding-top: 15px;
  }
  .row > h1 {
    padding-left: 15px;
  }
  .row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    cursor: pointer;
    padding: 20px;
  }
  .row__poster {
    max-height: 100px;
    object-fit: contain;
    cursor: pointer;
    margin-right: 15px;
    width: 100%;
    transition: transform 450ms;
    border: 3px solid white;
  }
  
  .row__posters::-webkit-scrollbar {
    display: none;
  }
  .row__poster:hover {
    transform: scale(1.1);
    opacity: 1;
  }
  
  .row__posterLarge {
    max-height: 250px;
    border: 3px solid white;
  }
  
  .row__posterLarge:hover {
    transform: scale(1.1);
    opacity: 1;
  }
  